
.works .BigHeader_container{
    margin-bottom: 80px;
}
.works .BigHeader{
    padding-bottom: 32px;
}
.work_study{
    background-image: url(../images/Our_works.png), url(../images/Our_works.png);
    background-repeat: no-repeat;
    background-position: top 0 right -10px,bottom 0 left -10px;
    background-size: 100%;
}
.works .orange_btn{
    margin-top: 40px;
}


@media (min-width:1024px) {
    .works .BigHeader_container > div{
        margin-bottom: 100px;
        display: flex;
       justify-content: space-between;
       column-gap: 100px;
       max-width: 100%;
    }
    /* .works .BigHeader_container{
       max-width: 98%;
     } */
     .works .BigHeader_text{
        padding-top: 16px;
     }
     .works .orange_btn{
        margin-top: 50px;
    }
    .work_study {
        background-position: top 0 right -50px,  bottom -90px left -250px;
        background-size: 500px;
    }
}
@media (min-width:1280px) {
    /* .works .BigHeader_container{
        max-width: 100%;
      } */
      .work_study{
        background-size: 700px;
    }
}
