.home .works{
    background-color: var(--green-50);
}
.home .green-banner{
    z-index: 10;
    background-image: url(../images/Homepage_1.png);
    background-repeat: no-repeat;
    background-position: bottom -100px right -10px;
    background-size: 200px;
}

.home > .BigHeader_container {
    background-image: url(../images/Homepage_1.png);
    background-repeat: no-repeat;
    background-position: top -150px right -10px;
    background-size: 200px;
    max-width: 100%;
}
.home .works{
    background-image: url(../images/Homepage_2.png);
    background-position: top -100px right -10px;
    background-size: 200px;
    background-repeat: no-repeat;
    max-width: 100%;
}
.home .works .study.top{
    display: none;
}
.home .works .BigHeader_container .btn{
    display: none;
}
.home .BigHeader_text{
    width: 95%;
    max-width: 525px;
}
.grid-image{
    width: 100%;
    max-width: 402px;
}
.work_study .work_right{
    padding-top: 0;
}


@media (min-width:1024px) {
    .home .green-banner{
        background-position: bottom -255px right -20px;
        background-size: 540px;
    }

    .home > .BigHeader_container{
        background-position: top -420px right -20px;
    background-size: 540px;
    }
    .home .works{
        background-position: top -350px right -20px;
    background-size: 540px;
    }
    .home .works .BigHeader_container > div{
       margin-bottom: 100px;
       display: block;
       
    }
}


@media (min-width:1280px) {
    .home .works{
        background-position: top -180px right -20px;
    background-size: 540px;
    }
    .home .BigHeader_text{
        max-width: 662px;
    }
    .grid-image {
        max-width: 492px;
        height: 491px;
    }
}